.meanings{
    display: flex;
    flex-direction: column;
    overflow: scroll;
    scrollbar-width: 3.5px;
    height: 55vh;
    border: 3.5px solid rgb(255, 255, 255);
    border-radius: 10px;
    padding: 10px 20px;
    overflow-x: hidden;
}
.subTitle{
    font-size: 5vw;
    font-family: "Montserrat", sans-serif;
}
@media (amx-width:900px) {
    .meanings{
        height: 60vh;
        overflow-x: hidden;
        overflow-y: scroll;
    }
}
.singleMean{
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    padding: 10px 20px;
    margin: 10px 0;
}